.main-header {
  padding: 0.5em;
  border-bottom: 0.0625rem solid #ccc; }
  @media (min-width: 30rem) {
    .main-header {
      padding: 1em 2em; } }

.content-grid {
  display: flex;
  flex-flow: row wrap;
  padding: 1em 0; }
  .content-grid__item {
    animation-name: fadeInUp;
    padding: 0 0.8em;
    width: 100%;
    margin-bottom: 1.5em; }
    @media (min-width: 30rem) {
      .content-grid__item {
        width: 50%; } }
    @media (min-width: 64rem) {
      .content-grid__item {
        width: 33%; } }
  .content-grid__image-wrapper {
    margin: 0 0 0.8em; }
  .content-grid__title {
    color: #3e3e3e;
    margin-top: 0;
    font-weight: normal; }

body,
html {
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
  color: #575757; }

a {
  color: #3e3e3e;
  text-decoration: none; }
  a:hover {
    color: #585858; }

h2 {
  margin-bottom: 0.3em;
  font-size: 1.5em; }

p {
  margin-top: 0;
  line-height: 1.5; }

.main-navigation {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s ease;
  text-align: center; }
  .main-navigation--open {
    max-height: 31.25rem; }
  @media (min-width: 30rem) {
    .main-navigation {
      max-height: none;
      text-align: right; } }
  .main-navigation__item {
    display: block;
    line-height: 1.875rem;
    padding: 0.4em; }
    @media (min-width: 30rem) {
      .main-navigation__item {
        display: inline;
        padding: 0;
        margin-left: 1em;
        font-size: 1.3em; } }

.hamburger {
  padding: 0.3125rem; }
  @media (min-width: 30rem) {
    .hamburger {
      display: none; } }
  .hamburger .hamburger-inner {
    background: #3e3e3e;
    height: 0.1875rem; }
    .hamburger .hamburger-inner:after, .hamburger .hamburger-inner:before {
      background: #3e3e3e;
      height: 0.1875rem; }

.main-footer {
  background: #e4e4e4;
  text-align: center;
  padding: 3em 2em; }

.container {
  display: flex;
  flex-flow: column nowrap; }
  @media (min-width: 45rem) {
    .container {
      flex-flow: row nowrap; } }
  .container__left {
    flex-basis: 100%; }
    @media (min-width: 45rem) {
      .container__left {
        flex-basis: 70%; } }
  .container__right {
    flex-basis: 100%;
    background: #d8d8d8;
    transition: transform 0.3s; }
    @media (min-width: 45rem) {
      .container__right {
        flex-basis: 30%;
        transform: translateX(100%);
        padding: 3em;
        text-align: center; } }
    .container__right--open {
      transform: translateX(0); }
